import Vue from 'vue'

//自定义组件
import App from './App.vue'

//全局引入 axios
import axios from 'axios'
//vue 继承axios
Vue.prototype.$axios = axios


Vue.config.productionTip = false
//第三方组件
// 搜索
import {
  Search
} from 'vant';
Vue.use(Search);
// 宫格
import {
  Grid,
  GridItem
} from 'vant';

Vue.use(Grid).use(GridItem);
//分割线
import {
  Divider
} from 'vant';
Vue.use(Divider);
//下拉刷新
import {
  DropdownMenu,
  DropdownItem
} from 'vant';

Vue.use(DropdownMenu).use(DropdownItem);



new Vue({
  render: h => h(App),
}).$mount('#app')