<template>
  <div>
    <h3>广州市千锋</h3>
    <!-- 搜索 -->
    <van-search
      background="#0085ff"
      shape="round"
      @search="onSearch"
      placeholder="搜索饿了么商家、商品名称"
      v-model="value"
    />
    <!-- 宫格 -->
    <van-grid>
      <van-grid-item
        v-for="(k,index) in kingkongListComputed"
        :key="index"
        :icon="k.icon"
        :text="k.name"
      />
    </van-grid>
    <!-- 分割线 -->
    <van-divider>推荐商家</van-divider>

    <!-- 下拉菜单 -->
    <van-dropdown-menu>
      <van-dropdown-item v-model="value1" :options="menu|handleMenu" />
      <van-dropdown-item v-model="value1" :options="menu|handleMenu" />
    </van-dropdown-menu>
  </div>
</template>


<script>
export default {
  data() {
    return {
      value: "",
      // 宫格
      kingkongList: [],
      //   下拉菜单
      value1: 0,
      menu: []
    };
  },

  methods: {
    onSearch() {}
  },
  computed: {
    kingkongListComputed() {
      // 切割成八个宫格
      return this.kingkongList.slice(8, 16);
    }
  },
  async created() {
    // console.log(this);

    let kingkongList = await this.$axios(
      "https://www.easy-mock.com/mock/5d3fe0fc738f621651cd1f4a/list/kingkong"
    );
    this.kingkongList = kingkongList.data.data.kingkongList;
    // this.entries = data.data[0].entries;
    // console.log(this.kingkongList);

    let menu = await this.$axios(
      "https://www.easy-mock.com/mock/5d3fe0fc738f621651cd1f4a/list/filterconditions"
    );
    this.menu = menu.data.data.sortVOList;
  },
  //   过滤器
  filters: {
    handleMenu(menu) {
      let newMenu = [];
      menu.forEach((element, index) => {
        //   往对象里面新增一个text属性值
        element.text = element.name;
        element.value = index;
        // 构造一个新的数组
        newMenu.push(element);
      });
      return newMenu;
    }
  }
};
</script>
